.payroll {
  position: relative;
}

.payroll .heading {
  text-align: center;
  top: 158px;
}

.payroll .solution {
  margin: 100px 213px;
  text-align: start;
}

.payroll .solution h2 {
  width: 600px;
}

.payroll .solution .block {
  display: flex;
  align-items: center;
  text-align: start;
  width: 1014px;
  justify-content: space-between;
}

.payroll .solution .block .item {
  width: 394px;
}

.payroll .slider {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin: 100px 125px 100px 213px;
}

.payroll .slider .links {
  text-align: start;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  height: 517px;
  padding: 40px 0px;
}

.payroll .slider .links div {
  cursor: pointer;
}

.payroll .slider .links div:hover, .payroll .slider .links div.active {
  color: #B166EB;
  background: linear-gradient(90deg, #F1A4DE 0%, #B166EB 50.52%, #7D39F5 100%), #FFFFFF;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
}

.payroll .slider .keen-slider, .payroll .slider .keen-slider .keen-slider__slide {
  width: 843px;
  height: 517px;
  background: #F4F7FC;
  border-radius: 30px;
}

.payroll .slider .keen-slider .keen-slider__slide {
  margin: 20px 50px;
  width: 600px;
  text-align: start;
}

.payroll .transfer {
  margin: 100px 20px;
  height: 1347px;
  color: white;
  position: relative;
}

.payroll .transfer .background {
  position: absolute;
}

.payroll .transfer h2 {
  position: relative;
  width: 600px;
  padding-top: 60px;
  margin: 0px auto;
}

.payroll .transfer .tree, .payroll .transfer .safety {
  position: relative;
  display: flex;
  margin: 50px 85px;
  align-items: center;
}

.payroll .transfer .tree .workers {
  margin-left: 30px;
  height: 320px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.payroll .transfer .tree .workers .worker {
  background-color: #21232B;
  border-radius: 10px;
  padding: 10px 20px 10px 10px;
}

.payroll .transfer .safety {
  margin-top: 100px;
  width: 488px;
  text-align: start;
}

.payroll .transfer .safety h3 {
  margin: 30px 0px;
}

.payroll .transfer .safety h2 {
  padding-top: 0px;
}

.payroll .transfer .safety h2 span {
  color: #B166EB;
  background: linear-gradient(90deg, #F1A4DE 0%, #B166EB 50.52%, #7D39F5 100%), #FFFFFF;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
}

.payroll .transfer .safety .body {
  color: #A5A9BB
}

.payroll .transfer .safety .boxes {
  display: flex;
}

.payroll .transfer .safety .boxes .row {
}

.payroll .transfer .safety .boxes .row .box {
  width: 290px;
  height: 218px;
  
  background: rgba(15, 7, 31, 0.5);
  box-shadow: 20px 4px 84px rgba(9, 3, 40, 0.7);
  backdrop-filter: blur(44px);
  border-radius: 15px;
  margin: 20px;
  display: flex;
  align-items: flex-start;
  flex-direction: column;
  justify-content: center;
  padding: 25px;
}

.payroll .transfer .safety .boxes .row .box h5 {
  color: #A5A9BB;
  margin-top: 30px;
}

.payroll .commodo {
  display: flex;
  align-items: flex-start;
  width: 100%;
  justify-content: space-between;
  height: 515px;
}

.payroll .commodo .commodo1 {
  align-self: baseline;
}

.payroll .commodo .commodo2 {
  align-self: flex-end;
}

.payroll .commodo .commodo3 {
  align-self: center;
}

.payroll .commodo .text {
  width: 600px;
  padding: 0 44px;
}

.payroll .earlyaccess {
  width: 1014px;
  height: 312px;
  margin: 100px 213px;
  position: relative;
  display: flex;
  align-items: center;
  text-align: start;
  justify-content: space-between;
}

.payroll .earlyaccess .background {
  position: absolute;
  top: 0px;
  left: 0;
}

.payroll .earlyaccess h1 {
  position: relative;
  margin: 50px;
  width: 490px;
}

.payroll .earlyaccess .item {
  position: relative;
  margin-right: 50px;
}