.App {
  text-align: center;
  background-color: #FFFFFF;
}

.App-logo {
  height: 30px;
}

ul li {
  list-style-type: disc;
  margin: 0 0 10px 40px;
}

h1 {
  font-family: 'Playfair Display', serif;
  font-style: normal;
  font-weight: 600;
  font-size: 76px;
  line-height: 100%;
  margin: 20px 0;
}

h2 {
  font-family: 'Playfair Display', serif;
  font-style: normal;
  font-weight: 600;
  font-size: 56px;
  line-height: 61px;
  margin: 30px 0;
}

h3 {
  font-family: 'Playfair Display';
  font-style: normal;
  font-weight: 500;
  font-size: 36px;
  line-height: 120%;
}

h4 {
  font-family: 'Epilogue', sans-serif;
  font-style: bold;
  font-weight: 700;
  font-size: 21px;
  line-height: 25px;
  margin: 20px 0;
}

h5 {
  font-family: 'Epilogue', sans-serif;
  font-style: normal;
  font-weight: 600;
  font-size: 18px;
  line-height: 22px;
}

.body {
  font-family: 'Epilogue', sans-serif;
  font-size: 16px;
  line-height: 26px;
}

.App-header {
  font-family: 'Epilogue';
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  font-size: calc(10px + 2vmin);
  margin: 42px 110px;
  height: 50px;
  line-height: 56px;
  font-weight: 600;
  font-size: 18px;
  width: 1220px;
  position: absolute;
  z-index: 100;
  background-color: white;
}

footer.App-header {
  position: relative;
  margin: 122px 110px 0 110px;
}

.footer-privacy {
  font-size: 14px;
  margin: 0 110px;
  display: flex;
  justify-content: space-between;
  line-height: 30px;
}

.footer-privacy div {
  display: flex;
  margin-right: 50px;
}

.footer-privacy div a {
  display: flex;
  margin-right: 50px;
}

.footer-privacy div img {
  margin-left: 20px;
}

.copyright { 
  font-size: 14px;
  color: #A5A9BB;
  text-align: start;
  padding: 30px 110px 50px 110px;
 }

.App-link {
  color: #61dafb;
}

.mobilemenu {
  display: none;
}

nav {
  display: flex;
  position: relative;
}

nav .item {
  margin-right: 50px;
  cursor: pointer;
  display: flex;
  align-items: center;
  width: fit-content;
}

nav .item:hover {
  color: #B166EB;
  background: linear-gradient(90deg, #F1A4DE 0%, #B166EB 50.52%, #7D39F5 100%), #FFFFFF;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
}

nav .item img, .button img, .button svg {
  margin: 0px 10px;
}

nav .button, .button {
  background: #171414;
  border-radius: 10px;
  color: #FFFFFF;
  padding: 0px 30px;
  line-height: 46px;
  height: 56px;
  cursor: pointer;
  width: fit-content;
  display: flex;
  align-items: center;
  justify-content: space-around;
}

.button.inverse {
  background: #FFFFFF;
  color: #B166EB;
}

.button.inverse span {
  background: linear-gradient(90deg, #F1A4DE 0%, #B166EB 50.52%, #7D39F5 100%), #FFFFFF;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
}

.button.inverse:hover {
  background: #171414;
}


nav .button:hover, .button:hover {
  color: #B166EB;
  background: #171414;
  -webkit-text-fill-color: initial;
}

nav .button:hover span, .button:hover span {
  background: linear-gradient(90deg, #F1A4DE 0%, #B166EB 50.52%, #7D39F5 100%), #FFFFFF;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
}

nav .button:active, .button:active {
  background: linear-gradient(90deg, #F1A4DE 0%, #B166EB 50.52%, #7D39F5 100%), #FFFFFF;
  color: #ffffff;
}

nav .button:active span, .button:active span {
  color: #ffffff;
  background: none;
  -webkit-text-fill-color: initial;
}


.submenu {
  position: absolute;
  width: 461px;
  padding: 20px;
  background: #FFFFFF;
  box-shadow: 0px 4px 34px rgba(37, 35, 130, 0.1);
  border-radius: 10px;
  left: -20px;
  top: 50px;
  flex-direction: column;
  align-items: flex-start;
  text-align: start;
  z-index: 11;
}

.contact.submenu {
  left: 0px;
  width: 100%;
  top: 80px;
  padding: 0px;
  display: flex;
  flex-direction: row;
  box-shadow: 0px 4px 74px -10px rgba(37, 35, 130, 0.1);
  border-radius: 20px;
  z-index: 10;
}

.contact .container {
  margin-left: 40px;
  padding: 20px;
}

.contact .closer {
  position: absolute;
  top: 20px;
  right: 20px;
  cursor: pointer;
}

.contact .block {
  display: flex;
  justify-content: space-between;
  margin-bottom: 30px;
}

.contact label {
  line-height: 24px;
}

.contact .MuiOutlinedInput-input {
  width: 251px;
  padding: 10px 15px;
  height: 34px;
  line-height: 1.3rem;
  font-size: 16px;
}

.contact .MuiInputBase-multiline {
  width: 100%;
  height: 168px;
  align-items: baseline;
}

.contact .MuiOutlinedInput-root {
  border-radius: 10px;
}

.contact .full .MuiFormControl-root {
  width: 100%;
}

.submenu .submenuitem {
  color: #000000;
  line-height: 30px;
  font-weight: bold;
  display: inline-flex;
  align-items: flex-start;
  -webkit-text-fill-color: initial;
}

.submenu .submenuitem:hover {
  color: #000000;
  background-color: #f0f0f0;
  -webkit-text-fill-color: initial;
}

.submenu .submenuitem img {
  margin: 0px 20px 0px 10px;
  float: left;
}

.submenu .submenuitem span {
  color: #7c7a7a;
  line-height: initial;
  display: block;
  font-size: 15px;
  line-height: 21px;
  padding-bottom: 10px;
  font-weight: normal;
}

.sticky {
  height: 100vh;
}

.footer-divider {
  margin: 25px 0;
}

@media only screen and (max-width: 1024px) {
  body {
    width: 100%;
  }

  header .footermenu {
    width: 100%;
    position: absolute;
    top: 50px;
    left: 0;
    z-index: 10;
    background-color: white;
  }

  header .footermenu nav {
    width: 100%;
    display: flex;
    flex-direction: column;
  }

  header .footermenu nav .item, header .footermenu .saudi {
    display: flex;
    margin: 0px auto;
  }

  header .footermenu .saudi {
    margin: 55px auto;
  }

  header .footermenu .social {
    display: flex;
    justify-content: center;
    margin-bottom: 40px;
  }

  header .footermenu .social img {
    margin-left: 20px;
  }

  .App-header {
    width: 100%;
    margin: 0;
    padding: 20px;
    position: initial;
    display: flex;
    justify-content: space-between;
  }

  .App-logo {
    height: 23px;
    max-width: none;
  }

  .App-header nav {
    display: none;
  }

  .mobilemenu {
    display: block;
  }

  h1 {
    font-size: 54px;
    line-height: 54px;
  }

  h2 {
    font-size: 36px;
    line-height: 43px;
  }

  h3 {
    font-size: 24px;
  }

  footer.App-header {
    display: block;
    margin: 30px 30px 0 30px; 
    width: calc(100% - 60px);
    height: auto;
  }

  footer.App-header nav {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
  }

  footer img {
    max-width: none;
  }

  footer .logo {
    display: flex;
    justify-content: center;
    align-items: center;
  }

  footer nav .item.button {
    margin: 10px auto;
    width: fit-content;
    display: flex;
    align-items: center;
    justify-content: space-around;
  }

  footer nav .item {
    margin-right: auto;
    text-align: center;
    width: 50%;
    display: initial;
  }

  .footer-privacy {
    display: initial;
  }

  .footer-privacy div, .footer-privacy div a {
    display: block;
    margin: 0;
  }

  .footer-privacy div.social {
    display: flex;
    justify-content: center;
    margin-top: 30px;
  }

  .copyright {
    padding: 20px 30px 20px 30px;
    text-align: center;
  }

  .contact.submenu {
    flex-direction: column;
    align-items: center;
    top: 45px;
  }

  .contact .blockcontainer {
    margin: 10px; 
  }

  .contact .block {
    display: block;
    margin-bottom: 0px;
  }

  .contact .container {
    margin: 20px;
  }

  .contact .block .MuiFormControl-root {
    margin-bottom: 30px;
    width: 100%;
  }

  .contact .button {
    width: 100%;
    justify-content: center;
  }
}
